import { Injectable } from '@angular/core';
import { Platform, ToastController, App, NavController, Tabs, Keyboard } from 'ionic-angular';
import { AudioPage } from '../../pages/audio/audio';

@Injectable()
export class BackButtonService {

    //false
    backButtonPressed: boolean = false;

    // 
    constructor(public platform: Platform, public keyboard: Keyboard,
        public appCtrl: App,
        public toastCtrl: ToastController) { }

    //
    registerBackButtonAction(tabRef: Tabs): void {

        //registerBackButtonAction

        this.platform.registerBackButtonAction(() => {
            if (this.keyboard.isOpen()) {//
                this.keyboard.close();
                return;
            }
            //modal
            let overlay = this.appCtrl._appRoot._overlayPortal.getActive() || this.appCtrl._appRoot._modalPortal.getActive();
            if (overlay) {
                let page = overlay.instance;
                if (page instanceof AudioPage) {//
                  if(page.isRecord){
                      page.stopRecord();
                  }
                  else{
                      page.playStop();
                  }
                  return;
                }
                else{
                    overlay.dismiss();
                } 
                return;
            }
            //NavController
            let activeNav: NavController = this.appCtrl.getActiveNav();
            //pop
            if (activeNav.canGoBack()) {
                activeNav.pop();
            } else {
                if (tabRef == null || tabRef._selectHistory[tabRef._selectHistory.length - 1] === tabRef.getByIndex(0).id) {
                    //
                    this.showExit();
                } else {
                    //
                    tabRef.select(0);
                }
            }
        });
    }

    //
    private showExit(): void {
        //true
        if (this.backButtonPressed) {
            this.platform.exitApp();
        } else {
            //Toast
            this.toastCtrl.create({
                message: '',
                duration: 2000,
                position: 'top'
            }).present();
            //true
            this.backButtonPressed = true;
            //false
            setTimeout(() => this.backButtonPressed = false, 2000);
        }
    }
}



